*, ::before, ::after {
  text-rendering: optimizeLegibility !important;
  -webkit-font-smoothing: antialiased !important;
  -moz-osx-font-smoothing: grayscale !important;
}

html {
  font-family: "Source Sans 3", sans-serif;
  color: var(--color-neutral-11);
}

body {
  font-size: var(--font-size-base);
  color: var(--color-neutral-11);
}

label {
  color: var(--color-neutral-11);
  font-size: var(--font-size-base);
  font-weight: 600;
  cursor: pointer;
}

body.has-accessible-features {
  /*input, textarea
  * &:focus-visible
  *  outline: none*/
}
body.has-accessible-features *:focus-visible {
  outline: 2px solid var(--color-focus-outer);
  outline-offset: 2px;
}
body.has-accessible-features * a:focus {
  box-shadow: none;
  background: inherit;
}
body.has-accessible-features *:focus {
  box-shadow: none;
}

a {
  text-decoration: underline;
  font-weight: var(--font-semi-bold);
  color: var(--color-primary);
}
a:hover {
  color: var(--color-neutral-11);
}

.heading1, h1 {
  color: var(--color-primary);
  font-family: "Roboto Slab", serif;
  font-weight: 700;
  margin-bottom: var(--layout-gap);
}

.heading2, h2 {
  color: var(--color-primary);
  font-weight: 700;
  margin-bottom: 1.25rem;
}

.heading3, h3 {
  color: var(--color-primary);
  font-weight: 700;
  margin-bottom: 1.25rem;
}

.heading4, h4 {
  color: var(--color-primary);
  font-weight: 700;
  margin-bottom: 1.25rem;
}

.heading5, h5 {
  color: var(--color-primary);
  font-weight: 700;
  margin-bottom: 1.25rem;
}

.heading6, h6 {
  color: var(--color-primary);
  font-weight: 700;
  margin-bottom: 1.25rem;
}

h1, h2, h3, h4, h5, h6 {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

blockquote {
  font-style: italic;
  border-left: var(--spacing-1) solid var(--color-neutral-4);
  padding-left: var(--spacing-4);
  padding-top: var(--spacing-1);
  padding-bottom: var(--spacing-1);
}

[data-block="OSUI_Content.Section"] {
  margin-top: var(--spacing-12);
  display: block;
}
[data-block="OSUI_Content.Section"] .section-title {
  font-size: var(--font-size-h2);
  font-weight: 700;
  border-color: var(--color-neutral-4);
  margin-bottom: var(--spacing-4);
  background: var(--color-neutral-0) !important;
}

body.dark-mode [data-block="OSUI_Content.Section"] .section-title {
  background-color: var(--color-neutral-2) !important;
}

section.card {
  background-color: var(--color-neutral-0);
  border: 1px solid rgba(var(--color-primary-rgb), 0.25);
  box-shadow: 0px 3px 20px 0px rgba(1, 62, 82, 0.12);
  margin-bottom: var(--layout-gap);
  margin-top: var(--layout-gap);
  padding: var(--layout-gap);
  padding-bottom: calc(var(--layout-gap) * 0.25);
}
body.dark-mode section.card {
  background-color: var(--color-neutral-2);
  border-color: var(--color-neutral-3);
  box-shadow: 0px 3px 20px 0px rgba(3, 7, 18, 0.75);
}
body.phone section.card {
  border-left: 0;
  border-right: 0;
  margin-left: calc(var(--layout-gap) * -1);
  margin-right: calc(var(--layout-gap) * -1);
}

.form-control[data-input] {
  border-color: var(--color-border) !important;
  background: var(--color-background-body);
  padding: var(--spacing-4);
  font-size: var(--font-size-base);
  color: var(--color-neutral-11);
  height: var(--spacing-14) !important;
}
.form-control[data-input]:focus, .form-control[data-input]:active {
  box-shadow: 0px 0px 0px var(--spacing-0-5) rgba(var(--color-primary-rgb), 0.25);
  border-color: var(--color-primary) !important;
}
body.has-accessible-features .form-control[data-input]:focus, body.has-accessible-features .form-control[data-input]:active {
  box-shadow: none;
}
.form-control[data-input]::placeholder {
  color: var(--color-neutral-6);
}

.form-control[data-textarea] {
  border-color: var(--color-border) !important;
  background: var(--color-background-body);
  padding: var(--spacing-4);
  font-size: var(--font-size-base);
  color: var(--color-neutral-11);
}
.form-control[data-textarea]:focus, .form-control[data-textarea]:active {
  box-shadow: 0px 0px 0px var(--spacing-0-5) rgba(var(--color-primary-rgb), 0.25);
  border-color: var(--color-primary) !important;
}

.input-with-icon .input-with-icon-content-icon {
  width: var(--spacing-12);
}

.input-with-icon .input-with-icon-content-icon:not(:empty) + .input-with-icon-input input {
  padding-left: var(--spacing-11);
}

.input-with-icon.input-with-icon-right .input-with-icon-content-icon:not(:empty) + .input-with-icon-input input {
  padding-right: var(--spacing-11);
}

.osui-search .form-control[data-input] {
  padding-left: var(--spacing-12);
}

.osui-search__input input[data-input] {
  background-image: url(data:image/svg+xml;base64,);
}

body.dark-mode div.content-counter {
  color: var(--color-neutral-7);
}

[data-switch]:empty:before {
  height: var(--space-m);
  width: var(--space-xl);
  background: var(--color-border);
  border-color: var(--color-border);
}
[data-switch]:empty:hover:before {
  background: var(--color-neutral-6);
  border-color: var(--color-neutral-6);
}
[data-switch]:empty:after {
  height: var(--spacing-4-5);
  width: var(--spacing-4-5);
  background: var(--color-background-body);
}
[data-switch]:checked:before, [data-switch]:checked:hover:before {
  background: var(--color-success);
  border-color: var(--color-success);
}
[data-switch]:checked:after {
  transform: translateX(var(--spacing-5)) translateZ(0);
}

.dropdown-container > div.dropdown-display, .dropdown-container > select.dropdown-display {
  border-color: var(--color-border) !important;
  background: var(--color-background-body);
  padding: var(--spacing-4);
  font-size: var(--font-size-base);
  color: var(--color-neutral-11);
  height: var(--spacing-14);
}
.dropdown-container > div.dropdown-display:focus, .dropdown-container > div.dropdown-display:active, .dropdown-container > select.dropdown-display:focus, .dropdown-container > select.dropdown-display:active {
  box-shadow: 0px 0px 0px var(--spacing-0-5) rgba(var(--color-primary-rgb), 0.25);
  border-color: var(--color-primary) !important;
}
.dropdown-container > div.dropdown-display:disabled, .dropdown-container > select.dropdown-display:disabled {
  background-color: var(--color-neutral-3);
  color: var(--color-neutral-9);
}

.dropdown-container:after {
  top: var(--spacing-4) !important;
  right: var(--spacing-4) !important;
  border: none;
  content: "\f107";
  font-family: "FontAwesome";
  font-size: var(--font-size-m);
  height: auto;
  width: auto;
  transform: none;
  color: var(--color-neutral-7);
}

/**
 * * DROPDOWN SERVERSIDE
 * */
div.osui-dropdown-serverside div.osui-dropdown-serverside__selected-values-wrapper {
  border-color: var(--color-border);
  background: var(--color-background-body);
  padding: var(--spacing-4);
  font-size: var(--font-size-base);
  color: var(--color-neutral-11);
  height: var(--spacing-14);
}

div.osui-dropdown-serverside__balloon-search-wrapper {
  background: var(--color-background-body);
}

div.osui-dropdown-serverside__balloon-wrapper div.osui-dropdown-serverside-item {
  background: var(--color-background-body);
  color: var(--color-neutral-11);
  height: var(--spacing-12) !important;
  padding-left: var(--spacing-4);
  padding-right: var(--spacing-4);
  background-position: bottom 50% right var(--spacing-4);
  background-size: var(--spacing-3);
  background-repeat: no-repeat;
  transition: color, background-color 180ms linear;
}
div.osui-dropdown-serverside__balloon-wrapper div.osui-dropdown-serverside-item:hover {
  background: var(--color-neutral-3);
  color: var(--color-primary);
}
div.osui-dropdown-serverside__balloon-wrapper div.osui-dropdown-serverside-item.osui-dropdown-serverside-item--is-selected {
