div.osui-overflow-menu__balloon nav a[data-link] {
  text-decoration: none;
  color: var(--color-neutral-11);
  padding: var(--spacing-2) var(--spacing-12) var(--spacing-2) var(--spacing-4);
}
div.osui-overflow-menu__balloon nav a[data-link] i:before {
  font-size: var(--font-size-s);
}
div.osui-overflow-menu__balloon nav a[data-link] span {
  margin-left: var(--spacing-2) !important;
}
div.osui-overflow-menu__balloon nav a[data-link]:hover {
  background: var(--color-neutral-3);
  color: var(--color-primary);
}
div.osui-overflow-menu__balloon nav a[data-link].active {
  background: var(--color-primary);
  color: var(--color-neutral-0);
}
div.osui-overflow-menu__balloon nav a[data-link].margin-bottom-s {
  margin-bottom: 0;
}

div.osui-progress-bar {
  --thickness: 21px !important;
  --progress-color: var(--color-primary) !important;
  --trail-color: var(--color-neutral-4) !important;
  --shape: sharp !important;
}

div.osui-progress-bar__container div.osui-progress-bar__content {
  color: var(--color-neutral-0);
  font-size: var(--font-size-s);
  font-weight: 600;
}

div.osui-progress-circle {
  --shape: sharp !important;
  --progress-color: var(--color-primary) !important;
  --trail-color: var(--color-neutral-4) !important;
}
div.osui-progress-circle div.osui-progress-circle__content {
  font-size: var(--font-size-m);
  font-weight: 600;
}